<template>
  <div class="resume-share">
    <img :src="qrUrl" class="resume-share__qr" />
  </div>  
</template>


<script>
import qr from "qr-image";
export default {
  name: "share",
  data() {
    return {
      qrUrl: ""
    };
  },
  mounted() {
    const code = qr.imageSync(location.href, {
      parse_url: location.origin,
      type: "png"
    });
    const url = "data:image/png;base64," + code.toString("base64");
    this.qrUrl = url;
  }
};
</script>

<style lang="postcss" scoped>
.resume-share {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
</style>




